import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { CHANGELOG_DISPLAY_COUNT } from '../utils/constants';

function InputPage() {
  const [input, setInput] = useState('');
  const [apiKey, setApiKey] = useState('');
  const [showHelp, setShowHelp] = useState(false);
  const [changelog, setChangelog] = useState([]);
  const navigate = useNavigate();
  const { apiKey: routeApiKey } = useParams(); //  apiKey

  useEffect(() => {
    const storedApiKey = localStorage.getItem('apiKey');
    if (storedApiKey) {
      setApiKey(storedApiKey);
    }
    if (routeApiKey) {
      setApiKey(routeApiKey);
      localStorage.setItem('apiKey', routeApiKey);
    }
  }, [routeApiKey]);

  useEffect(() => {
    // 
    const loadChangelog = async () => {
      try {
        const response = await fetch(`${process.env.PUBLIC_URL}/data/changelog.json`);
        const data = await response.json();
        
        // N
        const sortedUpdates = data.updates
          .sort((a, b) => {
            //  "25.8.4" 
            const dateA = new Date(`20${a.date.replace(/\./g, '/')}`);
            const dateB = new Date(`20${b.date.replace(/\./g, '/')}`);
            return dateB - dateA;
          })
          .slice(0, CHANGELOG_DISPLAY_COUNT);
        setChangelog(sortedUpdates);
      } catch (error) {
        console.error(':', error);
        // 
        setChangelog([{
          date: "",
          version: "",
          description: ""
        }]);
      }
    };

    loadChangelog();
  }, []);

  const handleApiKeyChange = (e) => {
    const newApiKey = e.target.value;
    setApiKey(newApiKey);
    localStorage.setItem('apiKey', newApiKey);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    let finalInput = input;
    let finalApiKey = apiKey;

    if (!finalInput) {
      if (process.env.REACT_APP_LOGS_ID !== undefined) {
        finalInput = process.env.REACT_APP_LOGS_ID; // 
        setInput(finalInput); // 
      } else {
        alert(' FFLogs  ID');
        return;
      }
    }
    if (!finalApiKey) {
      if (process.env.REACT_APP_API_KEY !== undefined) {
        finalApiKey = process.env.REACT_APP_API_KEY; //  API_KEY
        setApiKey(finalApiKey); // 
      } else {
        alert(' API_KEY');
        return;
      }
    }

    const sanitizedInput = finalInput.split('&')[0]; //  & 
    const urlPattern = /^(?:https?:\/\/)?(?:[a-zA-Z0-9-]+\.)?fflogs\.com\/reports\/(a:[a-zA-Z0-9]+|[a-zA-Z0-9]+)(?:[?#]fight=([a-zA-Z0-9]+))?$|^[a-zA-Z0-9]{16,}$/;
    const match = sanitizedInput.match(urlPattern);

    if (match) {
      const logsId = match[1] || sanitizedInput; //  ID
      const fightId = match[2];
      if (logsId) {
        navigate(fightId ? `/${finalApiKey}/${logsId}/${fightId}` : `/${finalApiKey}/${logsId}`);
      }
    } else {
      alert(' FFLogs  ID ');
    }
  };

  const generateBookmarkLink = () => {
    return `${window.location.origin}/fflogs_phase_ranker/#/${apiKey}`;
  };

  // 
  const getLatestVersion = () => {
    if (changelog.length > 0 && changelog[0].version) {
      return changelog[0].version;
    }
    return null;
  };

  const latestVersion = getLatestVersion();

  return (
    <div className="container mt-5">
      <h1 className="text-center">FFLogs P
        {latestVersion && (
          <span className="badge bg-secondary ms-2" style={{ fontSize: '0.5em', verticalAlign: 'middle' }}>
            {latestVersion}
          </span>
        )}
      </h1>
      <form onSubmit={handleSubmit} className="mt-4">
        <div className="mb-3">
          <label htmlFor="logIdInput" className="form-label"> FFLogs  ID</label>
          <input
            type="text"
            id="logIdInput"
            className="form-control"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="https://www.fflogs.com/reports/QrStUvWx12345678?fight=1  QrStUvWx12345678"
          />
        </div>
        <div className="mb-3">
          <label htmlFor="apiKeyInput" className="form-label"> API_KEY</label>
          <input
            type="text"
            id="apiKeyInput"
            className="form-control"
            value={apiKey}
            onChange={handleApiKeyChange}
            placeholder=" API_KEY"
          />
          <button
            type="button"
            className="btn btn-link mt-2"
            onClick={() => setShowHelp(!showHelp)}
          >
            {showHelp ? '' : ' API_KEY?'}
          </button>
          {showHelp && (
            <div className="p-3 mt-2" style={{ backgroundColor: '#f8f9fa', border: '1px solid #ddd' }}>
              <p> API_KEY</p>
              <ol>
                <li> FFLogs </li>
                <li> <a href="https://cn.fflogs.com/profile" target="_blank" rel="noopener noreferrer">FFLogs </a>API</li>
                <li><span style={{ fontWeight: 'bold', color: 'red', fontSize: '1.3em' }}>V1</span>V1</li>
              </ol>
              <p> API_KEY</p>
              <p>API_KEY</p>
              <a href={generateBookmarkLink()} target="_blank" rel="noopener noreferrer" className="btn btn-outline-secondary">
                 API_KEY 
              </a>
            </div>
          )}
        </div>
        <div className="row g-2">
          <div className="col-7">
            <button type="submit" className="btn btn-primary w-100"></button>
          </div>
          <div className="col-5">
            <button
              type="button"
              className="btn w-100"
              style={{
                backgroundColor: '#ffd700',
                color: '#222',
                fontWeight: 'bold',
                border: '2px solid #ffb800',
                boxShadow: '0 2px 8px rgba(255,215,0,0.15)',
              }}
              onClick={() => navigate('/dataset')}
            >
              
            </button>
          </div>
        </div>
      </form>
      {/*  */}
      <div className="mb-4 p-3 mt-4 border rounded bg-light">
        <h5 className="mb-3 fw-bold"></h5>
        <style>{`
          .changelog-date { min-width: 60px; }
          .changelog-version { min-width: 55px; }
        `}</style>
        <div className="small">
          {changelog.length > 0 ? (
            changelog.map((update, index) => (
              <div key={index} className="mb-2 d-flex align-items-start">
                <span className="badge bg-primary me-2 changelog-date">{update.date}</span>
                <span className="badge bg-secondary me-3 changelog-version">{update.version}</span>
                <span>{update.description}</span>
              </div>
            ))
          ) : (
            <div className="text-muted">...</div>
          )}
        </div>
      </div>
      {/*  */}
      <div className="mb-4 p-3 border rounded bg-light">
        <h5 className="mb-3 fw-bold"></h5>
        <div className="small">
          <a href="https://space.bilibili.com/12348381" target="_blank" rel="noopener noreferrer">_DreamWalker</a>
          <a href="https://space.bilibili.com/8900735" target="_blank" rel="noopener noreferrer">leifeng</a>
          <br />
          by <a href="https://space.bilibili.com/522021" target="_blank" rel="noopener noreferrer">ITX351</a>@
        </div>
      </div>
    </div>
  );
}

export default InputPage;
